import React, {useState, useEffect} from 'react';
import './BoardPage.css';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Thread } from './Thread';
import { NewThreadForm } from "./NewThreadForm/NewThreadForm";


export const BoardPage = ({match}) => {
    const [threads, setThreads] = useState([]);
    const [threadInfo, setThreadInfo] = useState({posts: []});
    const id = match.params.id;
    const [isCreate, setCreate] = useState(false);

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/board/${id}/`
        }).then((res) => {
            let data = res.data
            data.posts.sort((a, b) => {
                return b.id - a.id
            })
            setThreadInfo(data);
        })
    }, [id])

    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/board/"
        }).then((res) => {
            setThreads(res.data);
        })
    }, [])

    function handleClick() {
        setCreate(!isCreate);
    }

    const noThreads = <div className="board__nothreads">
        There are no any threads yet
    </div>

    return (
        <div className="board">
            <div className="board__header">
                <h1 className="header__boardname">{threadInfo ? threadInfo.name : 'Loading'}</h1>
                <div className="board__create">
                    <span onClick={handleClick}>{isCreate ? "Close form" : "Сreate thread"}</span>
                    {isCreate && <NewThreadForm boardName={threadInfo.name}/>}
                </div>
            </div>
            <div className="board__wrapper">
                <aside className="board__navigation">
                    {threads.map((elem) => {
                        return <Link to={{pathname: `/board/${elem.id}`, fromDashboard: false}} key={elem.id}
                                     className='board__threadLink'>{elem.name}</Link>
                    })}
                </aside>
                <div className="board__threadsList">
                    {threadInfo && threadInfo.posts.length !== 0 ? threadInfo.posts.map((elem) => {
                            const date = new Date(elem.pub_date);
                            return <Thread key={elem.id} content={elem.content} title={elem.title} date={date.toLocaleString()}
                                           number={elem.slug.slice(elem.slug.indexOf('-') + 1)}/>
                        }
                    ) : {noThreads}
                    }
                </div>
            </div>
        </div>
    )
}